.App {
    display: flex;
    height: 100%;
    width: 100%;
    position: fixed;
}

/* Mobile/touchscreen */
@media (hover: none) {
    .App {
        flex-direction: column-reverse;
    }

    .search-box {
        flex: 1
    }
}

/* With a mouse */
@media (hover: hover) {
    .App {
        flex-direction: column;
    }
}

.verb-form {
    flex: 0 0 auto;
    height: auto;
    padding: 8px;
    background: lightgray;
    display: flex;
    flex-direction: row;
}

.search-box {
    border: 1px solid black;
    border-radius: 4px;
    width: max-content;
    display: flex;
    flex-direction: row;
    margin-right: 8px;
}

.search-box-text {
    flex: 1;
    padding: 4px;
    border: none;
    background: transparent;
    outline: none;
    font-size: large;
}

.search-box-button {
    flex: 0 0 auto;
    width: auto;
    border: none;
    background: none;
    cursor: pointer;
}

button {
    cursor: pointer;
}

.tables-container {
    overflow: auto;
    flex: 1;
}

.selected-verbs-default {
    background: lightgray;
    color: gray;
    padding: 8px;
}

.selected-verbs-list {
    background: lightgray;
}

.selected-verbs-list button {
    background: transparent;
    border: none;
    padding: 0 8px 0 0;
}

.selected-verbs-entry {
    padding: 8px;
}
