.verb-table {
    border-collapse: separate;
    border-spacing: 0;
}

.verb-table td, .verb-table th {
    border-bottom: 1px solid black;
    border-right: 1px solid black;
    margin: 0;
    padding: 4px;
}

.verb-table td:first-child, .verb-table th:first-child {
    border-left: 1px solid black;
}

.verb-table th {
    border-top: 1px solid black;
}

.verb-table th:first-child {
    border-top-left-radius: 4px;
}

.verb-table th:last-child {
    border-top-right-radius: 4px;
}

.verb-table tr:last-child td:first-child {
    border-bottom-left-radius: 4px;
}

.verb-table tr:last-child td:last-child {
    border-bottom-right-radius: 4px;
}

.verb-collapsable {
    margin: 8px;
    cursor: pointer;
}
